.profile-picture {
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 5%;
	width: 80%;
	border-radius: 50%;
	border: var(--light-gray) 1px solid;
}

.profile-picture:hover {
	opacity: 0.7;
}

@media only screen and (max-width: 1000px) {
	.profile-picture {
		margin: 0.5rem;
		width: 4rem;
	}
}